<template>
  <aside class="filter">
    <form
      class="filter__form form"
      action="#"
      method="get"
      @submit.prevent="submit"
    >
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price">
          <input
            class="form__input"
            type="text"
            name="min-price"
            value=""
            :placeholder="minPrice"
          />
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price">
          <input
            class="form__input"
            type="text"
            name="max-price"
            value=""
            :placeholder="maxPrice"
          />
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select">
          <select class="form__select" type="text" name="category">
            <option value="0">Все категории</option>
            <option
              v-for="category in categories"
              :value="category.id"
              :key="category.id"
            >
              {{ category.title }}
            </option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Материал</legend>
        <ul class="check-list">
          <li
            class="check-list__item"
            v-for="material in materials"
            :key="material.id"
          >
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                :value="material.id"
              />
              <span class="check-list__desc">
                {{ material.title }}
                <span>({{ material.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Коллекция</legend>
        <ul class="check-list">
          <li
            class="check-list__item"
            v-for="season in seasons"
            :key="season.id"
          >
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                :value="season.id"
              />
              <span class="check-list__desc">
                {{ season.title }}
                <span>({{ season.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit">
        Применить
      </button>
      <button class="filter__reset button button--second" type="button">
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>
// eslint-disable-next-line
import axios from 'axios';
import { API_BASE_URL } from '../config';

export default {
  props: ['products'],
  data() {
    return {
      minPrice: null,
      maxPrice: null,

      categories: [],
      materials: [],
      seasons: [],
    };
  },
  methods: {
    calculatePrices() {
      const prices = this.products.map((product) => product.price);
      this.minPrice = Math.min(...prices);
      this.maxPrice = Math.max(...prices);
    },
    async loadCategories() {
      try {
        const res = await axios.get(`${API_BASE_URL}/api/productCategories`);
        this.categories = res.data.items;
      } catch (error) {
        console.error('Произошла ошибка при загрузке категорий:', error);
      }
    },
    async loadMaterials() {
      try {
        const res = await axios.get(`${API_BASE_URL}/api/materials`);
        this.materials = res.data.items;
      } catch (error) {
        console.error('Произошла ошибка при загрузке материалов:', error);
      }
    },
    async loadSeasons() {
      try {
        const res = await axios.get(`${API_BASE_URL}/api/seasons`);
        this.seasons = res.data.items;
      } catch (error) {
        console.error('Произошла ошибка при загрузке сезонов:', error);
      }
    },
  },
  watch: {
    products: 'calculatePrices',
  },
  created() {
    this.loadCategories();
    this.loadMaterials();
    this.loadSeasons();
  },
};
</script>
